import api from "../../config/api.js"
import Util from "../../utils/utils"

export default {
  name: 'record',
  data () {
    return {
      token: '',
      roomId: '',
      num: 0,
      owner: 0,
      points: 0,
      recordlist: null,
      scrollBottom: false,
      count: 0,
      pageSize: 10
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    getRank(count, fn) {
      api.Axios.get(api.RECORD,{
        params: {
          page: count,
          size: this.pageSize,
        }
      }).then(res => {
        console.log('res', res);
        if (res.data.code === 200) {
          this.recordlist = res.data.data.aokePowerRecords
        } else {
          this.$toast(res.data.msg, 1500)
        }

        if (res.data && (res.data.code === 200)) {
          let recordlist = res.data.data.aokePowerRecords.content
          if (res.data.data.aokePowerRecords.last) {
            // this.count = 0
            this.scrollBottom = true
            fn(true)
          } else {
            if (fn) fn()
          }
          if (count === 1) {
            this.recordlist = recordlist
          } else {
            this.recordlist = this.recordlist.concat(recordlist)
          }
        } else {
          this.scrollBottom = true
          fn(true)
          this.recordlist = null
          // this.msg = res.data.msg
        }
      })
    },
    infinite (done) {
      if (this.scrollBottom) {
        done(true)
        return
      }
      this.count++
      this.getRank(this.count, done)
    },
    refresh (done) {
      this.count = 0
      this.scrollBottom = false
      this.getRank(1, done)
    }
  }
}



