
import XTable from 'vux/src/components/x-table/index.vue'

import api from "../config/api.js"
import Util from "../utils/utils"

export default {
components: {
XTable
},
data() {
return {
token: "",
currentRoomId: "",
totalNum: 0,
rank: 0,
loading: true,
tip: false,
rankList: null
}
},
created() {
},
mounted() {
this.getRank()
},
methods: {
getRank() {
api.Axios.get(api.RANK).then(res => {
console.log('res', res);
if (res.data.code === 200) {
this.totalNum = res.data.data.aokeUserSize
this.rank = res.data.data.minePowerRank
this.rankList = res.data.data.aokeUsers.content
} else {
this.$toast(res.data.msg, 1500)
}
})
}
}
}



