import api from '../../config/api.js'
import Util from '../../utils/utils'

export default {
  name: 'sign',
  data () {
    return {
      tips: ""
    }
  },
  methods: {
    signIn() {
      api.Axios.get(api.SIGNIN).then(res => {
        if (res.data.code === 200) {
          this.tips = res.data.data
          this.$toast(this.tips, 1500)
        } else {
          this.$toast(res.data.msg, 1500)
        }
      })
    }
  }
}



