import api from "../../config/api.js"
import Util from "../../utils/utils"

export default {
  data () {
    return {
      msg: '',
      personalInfo: null,
    }
  },
  created() {
  },
  mounted() {
    this.getUserInfo()
  },
  methods: {
    getUserInfo() {
      api.Axios.get(api.USERINFO).then(res => {
        if (res.data.code === 200) {
          this.personalInfo = res.data.data.aokeUser
        } else {
          this.$toast(res.data.msg, 1500)
        }
      })
    }
  }
}



