
import api from "../config/api.js"
import Util from "../utils/utils"
import countdown from "./public/countdown.vue"
import  Velocity from 'velocity-animate'
import Toast from 'vux/src/components/toast/index.vue'


export default {
components: {
Toast,
countdown
},
data() {
return {
token: "",
currentRoomId: "",
points: 0,
powers: 0,
stoneList: null,
countDownTime: 0,
city: "",
area: "",
status: "",
name: "",
num: "",
id: "",
clickAble: true,
loading: true,
tip: false,
count: 0,
showFirst: true,
showSecond: false,
showWait: false,
userInfo1:[],
userInfo2:[],
userInfo3:[],
userInfo4:[],
}
},
created() {
this.init()
},
computed: {
},
watch: {
},
mounted() {
this.getParam()
},
methods: {
init() {
this.getToken()
this.getRoomId()
},
getToken() {
this.token = Util.pageUrlGetValue("token")
Util.setCookie("token", this.token, "aylives.cn")
},
getRoomId() {
this.currentRoomId = Util.pageUrlGetValue("currentRoomId")
Util.setCookie("currentRoomId", this.currentRoomId, "aylives.cn")
},
getParam() {
api.Axios.get(api.MAIN).then(res => {
if (res.data.code === 200) {
this.points = res.data.data.aokeUser.aokeWallet.aokePoints
this.powers = res.data.data.aokeUser.aokeWallet.aokePower
this.stoneList = res.data.data.temporaryPoints
this.countDownTime = res.data.data.nextGenerateTime
if (res.data.data.temporaryPoints && res.data.data.temporaryPoints.length > 0) {
res.data.data.temporaryPoints.forEach( (element, index, array) => {
if (index <= 5) {
this.userInfo1.push(element)
console.log('1', this.userInfo);
} else if (index <= 11) {
this.userInfo2.push(element)
console.log('2', this.userInfo2);
} else if (index <= 17) {
this.userInfo3.push(element)
console.log('3', this.userInfo3);
} else if (index <= 23) {
this.userInfo4.push(element)
console.log('4', this.userInfo4);
} else {
this.$toast(res.data.msg, 1500)
}
})
} else {
this.showWait = true
}
} else {
this.$toast(res.data.msg, 1500)
}
})
},
collectStone(pointId) {
api.Axios.get(api.COLLECT_STONE(pointId)).then(res => {
console.log('res', res);
if (res.data.code === 200) {
this.points = res.data.data.aokeUser.aokeWallet.aokePoints
this.powers = res.data.data.aokeUser.aokeWallet.aokePower
this.stoneList = res.data.data.temporaryPoints
this.countDownTime = res.data.data.nextGenerateTime

if (!this.stoneList) {
this.showWait = true
}

} else {
this.$toast(res.data.msg, 1500)
}
})
},
collect (item) {
let index = item.id
let oId = document.getElementById(`id${index}`)
this.count++



Velocity(oId,{
top: 40,
opacity: 0
}, {
duration: 1000,
complete: function() {

}
})









this.collectStone(item.id)









},
refresh (point) {
this.points += point
},
}
};



