
import XCircle from 'vux/src/components/x-circle/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import VueCoreImageUpload from 'vue-core-image-upload'
import Api from '../../../config/api.js'
import { setTimeout } from 'timers'
import selectModel from '../../public/selectModel'

export default {
directives: {
TransferDom
},
components: {
XCircle,
XButton,
XTextarea,
VueCoreImageUpload,
Flexbox,
FlexboxItem,
selectModel
},
data () {
return {
showUpload: true,
uploadText: '',
uploadUrl: Api.ADD_PHOTO,
form: {
openId: '',
uploadPics: []
},
uploadImg1: '',
pushBtn: true,
content: '',
selected: '书籍',
selectModel: {
showSelect: false,
selectList: ['书籍', '母婴', '家居', '美妆', '数码', '鞋服', '其他']
}
}
},





methods: {
pushPhoto () {
let params = {
content: this.content,
photoList: this.form.uploadPics
}
Api.Axios.post(Api.PUSH, params).then(res => {
if (res.data.code === 200) {
this.$toast('发表成功', 1000)
setTimeout(() => {
this.$router.push({path: '/happiness'})
}, 120)
} else {
this.$toast(res.data.msg, 1000)
}
})
},
push () {
let params = {
content: this.content,
photoList: this.form.uploadPics
}
Api.Axios.post(Api.PUSH, params).then(res => {
if (res.data.code === 200) {
this.$toast('发表成功', 1000)
setTimeout(() => {
this.$router.push({path: '/happiness'})
}, 120)
} else {
this.$toast(res.data.msg, 1000)
}
})
},
imageuploaded (res) {
if (this.form.uploadPics.length === 3) {
this.$toast('最多可上传三张图片', 1000)
}

if (res.code === 200) {
let photoUrl = res.data.photo
this.form.uploadPics.push(photoUrl)
if (this.form.uploadPics.length === 3) {
this.showUpload = false
}
} else {
this.$toast(res.msg, 1000)
}
},
delImage (index) {
this.form.uploadPics.splice(index, 1)
if (this.form.uploadPics.length < 3) {
this.showUpload = true
}
},
imageuploading () {




},
errorhandle (err) {
if (err) {
this.$toast('图片不可以超过10MB', 1000)
}
},
showSelect() {
this.selectModel.showSelect = true
},
getSelected(selectedOpt) {
this.selected = selectedOpt
console.log("push-getSelected", this.selected);
}
}
}



